import PropTypes from "prop-types";
import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { getProductCartQuantity } from "../../helpers/product";
import { addToCart } from "../../redux/actions/cartActions";
import { addToWishlist } from "../../redux/actions/wishlistActions";
import { addToCompare } from "../../redux/actions/compareActions";
import Rating from "./sub-components/ProductRating";
import { Collapse } from 'react-bootstrap';
import MapViewBike from "../map/product-location";

const ProductDescriptionInfo = ({
  product,
  discountedPrice,
  currency,
  finalDiscountedPrice,
  finalProductPrice,
  cartItems,
  wishlistItem,
  compareItem,
  addToast,
  addToCart,
  addToWishlist,
  addToCompare,
}) => {
  const [open, setOpen] = useState(false);
  const [mapVisible, setMapVisible] = useState(false);

  const [selectedProductColor, setSelectedProductColor] = useState(
    product.variation ? product.variation[0].color : ""
  );
  const [selectedProductSize, setSelectedProductSize] = useState(
    product.variation ? product.variation[0].size[0].name : ""
  );
  const [productStock, setProductStock] = useState(
    product.variation ? product.variation[0].size[0].stock : product.stock
  );
  const [quantityCount, setQuantityCount] = useState(1);

  const productCartQty = getProductCartQuantity(
    cartItems,
    product,
    selectedProductColor,
    selectedProductSize
  );
  console.log(product.shopRelation.longitude);
  return (
    <div className="product-details-content ml-70">
      <h2>{product.name}</h2>
      <div className="product-details-price">
        {discountedPrice !== null ? (
          <Fragment>
            <span>{product.price}</span>{" "}
            <span className="old">{product.price}</span>
          </Fragment>
        ) : (
          <span>{product.price} </span>
        )}
      </div>
      {product.rating && product.rating > 0 ? (
        <div className="pro-details-rating-wrap">
          <div className="pro-details-rating">
            <Rating ratingValue={product.rating} />
          </div>
        </div>
      ) : (
        ""
      )}
      <div className="p-2">
        <div className="w-100 p-2 bg-gray">
          <div className="mb-2">
            <h4>Get In Touch</h4>
          </div>
          <form>
            <div className="row">
              <div class="form-group col-12">
                <textarea class="form-control" placeholder="Message..." rows="2"></textarea>
              </div>
            </div>
            <div className="row mb-1">
              <div className="col-lg-6">
                <input className="form-control" name="name" placeholder="First Name" type="text" />
              </div>
              <div className="col-lg-6">
                <input className="form-control" name="name" placeholder="Last Name" type="text" />
              </div>
            </div>
            <div className="row mb-1">
              <div className="col-lg-6">
                <input className="form-control" name="email" placeholder="Email" type="text" />
              </div>
              <div className="col-lg-6">
                <input className="form-control" name="phone" placeholder="Phone(optimal)" type="text" />
              </div>
            </div>
            <div>
              <button type="button" class="btn btn-lg btn-block button-color" type="submit">SEND</button>
            </div>
          </form>
        </div>
        <div>
          <div className="p-2">
            <div className="d-flex m-0 justify-content-between align-content-center">
              <div>
                Show on map
              </div>
              <div>
                <button
                  onClick={() => setOpen(!open)}
                  aria-expanded={open}
                  className="button-color btn"
                >
                  <span class="fa fa-map-marker" aria-hidden="true"></span>
                </button>
              </div>
            </div>
          </div>
          <Collapse
            in={open}
            mountOnEnter={true}
            onEntered={() => setMapVisible(true)}
          >
            <div className="bg-gray" style={{
              height: 400,
              width: '100%',
            }}>
              {mapVisible ? <MapViewBike
                shopLat={product.shopRelation.latitude}
                shopLong={product.shopRelation.longitude}
              /> : "Loading map ..."
              }
            </div>
          </Collapse>

        </div>
      </div>
    </div>
  );
};

ProductDescriptionInfo.propTypes = {
  addToCart: PropTypes.func,
  addToCompare: PropTypes.func,
  addToWishlist: PropTypes.func,
  addToast: PropTypes.func,
  cartItems: PropTypes.array,
  compareItem: PropTypes.array,
  currency: PropTypes.object,
  discountedPrice: PropTypes.number,
  finalDiscountedPrice: PropTypes.number,
  finalProductPrice: PropTypes.number,
  product: PropTypes.object,
  wishlistItem: PropTypes.object,
};

const mapDispatchToProps = (dispatch) => {
  return {
    addToCart: (
      item,
      addToast,
      quantityCount,
      selectedProductColor,
      selectedProductSize
    ) => {
      dispatch(
        addToCart(
          item,
          addToast,
          quantityCount,
          selectedProductColor,
          selectedProductSize
        )
      );
    },
    addToWishlist: (item, addToast) => {
      dispatch(addToWishlist(item, addToast));
    },
    addToCompare: (item, addToast) => {
      dispatch(addToCompare(item, addToast));
    },
  };
};

export default connect(null, mapDispatchToProps)(ProductDescriptionInfo);
