import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
// create a reusable component that take popup name zomm lon and lat 
import LeafletMap from '../leaflet-map/LeafletMap';


const MapViewBike = ({ shopLat, shopLong }, props) => {

  return <LeafletMap
    lat={shopLat}
    lon={shopLong}
    zoom={12}
    popupText="shop name"
    onMarkerClick={() => console.log("Marker was clicked")}
    onpupClick={() => console.log("popup was clicked")}
  />
}
MapViewBike.propTypes = {
  shops: PropTypes.array,
};

const mapStateToProps = (state) => {
  return {
    shops: state.shopData.shops,
  };
};

export default connect(mapStateToProps)(MapViewBike);
