import PropTypes from "prop-types";
import React, { Fragment, useEffect, useState } from "react";
import { LightgalleryProvider, LightgalleryItem } from "react-lightgallery";
import Swiper from "react-id-swiper";

const ProductImageGallery = ({ product }) => {
  const [gallerySwiper, getGallerySwiper] = useState(null);
  const [thumbnailSwiper, getThumbnailSwiper] = useState(null);
  // effect for swiper slider synchronize
  // images should be array and not string
  let imageSet = [];
  if(product && product.imageSet && product.imageSet.length == 1 && !product.imageSet[0].url){
    console.log("replace image");
    imageSet = [{url: '/assets/img/product/bike-image-not-found.png'}];
  }
  else imageSet = product.imageSet;
  const sliderImages = Array.isArray(product.images) ? product.images : imageSet;
  useEffect(() => {
    if (
      gallerySwiper !== null &&
      gallerySwiper.controller &&
      thumbnailSwiper !== null &&
      thumbnailSwiper.controller
    ) {
      gallerySwiper.controller.control = thumbnailSwiper;
      thumbnailSwiper.controller.control = gallerySwiper;
    }
  }, [gallerySwiper, thumbnailSwiper]);

  // swiper slider settings
  const gallerySwiperParams = {
    getSwiper: getGallerySwiper,
    spaceBetween: 10,
    loopedSlides: 4,
    loop: true,
    effect: "fade",
  };

  const thumbnailSwiperParams = {
    getSwiper: getThumbnailSwiper,
    spaceBetween: 10,
    slidesPerView: 4,
    loopedSlides: 4,
    touchRatio: 0.2,
    freeMode: true,
    loop: true,
    slideToClickedSlide: true,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    renderPrevButton: () => (
      <button className="swiper-button-prev ht-swiper-button-nav">
        <i className="pe-7s-angle-left" />
      </button>
    ),
    renderNextButton: () => (
      <button className="swiper-button-next ht-swiper-button-nav">
        <i className="pe-7s-angle-right" />
      </button>
    ),
  };
  return (
    <Fragment>
      <div className="product-large-image-wrapper">
        {product.discount || product.new ? (
          <div className="product-img-badges">
            {product.discount ? (
              <span className="pink">-{product.discount}%</span>
            ) : (
              ""
            )}
            {product.new ? <span className="purple">New</span> : ""}
          </div>
        ) : (
          ""
        )}
        <LightgalleryProvider>
          <Swiper {...gallerySwiperParams}>
            {imageSet &&
              imageSet.map((single, key) => {
                return (
                  <div key={key}>
                    <LightgalleryItem group="any" src={single.url}>
                      <button>
                        <i className="pe-7s-expand1"></i>
                      </button>
                    </LightgalleryItem>

                    <div className="single-image">
                      <div style={{
                        backgroundColor:'white',
                        backgroundImage: `url(${single.url})`, 
                        width:'100%', 
                        height:400, 
                        backgroundSize:'contain', 
                        backgroundRepeat:'no-repeat', 
                        backgroundPosition:'center center'
                        }} />
                    </div>
                  </div>
                );
              })}
          </Swiper>
        </LightgalleryProvider>
      </div>
      <div className="product-small-image-wrapper mt-15">
        <Swiper {...thumbnailSwiperParams}>
          {sliderImages && 
            sliderImages.map((single, key) => {
              return (
                <div key={key}>
                  <div className="single-image">
                      <div style={{
                        backgroundColor:'white',
                        backgroundImage: `url(${single.url})`, 
                        width:'100%', 
                        height:100, 
                        backgroundSize:'contain', 
                        backgroundRepeat:'no-repeat', 
                        backgroundPosition:'center center'
                        }} />
                    </div>
                </div>
              );
            })}
        </Swiper>
      </div>
    </Fragment>
  );
};

ProductImageGallery.propTypes = {
  product: PropTypes.object,
};

export default ProductImageGallery;
